import {
    Menu,
    CardActionArea, 
    Typography, 
    Box, 
    FormHelperText, 
    Grid,
    Stack,
} from '@mui/material';
import collaborationLounge from '../icons/icons8-collaboration-client-64.png';
import workspaceIconImage from '../icons/icons8-mallette-48.png';
import medialibraryIconImage from '../icons/icons8-aparat-48.png';
import archiveIconImage from '../icons/icons8-file-64.png';
import censorIconImage from '../icons/icons8-privé-64.png';
import ImgToIcon from '../icons/ImgToIcon';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

const isAuth = (permissions,...apps) => 
    !![...apps].find(app => permissions?.find(auth => auth === app)
);

export default function AppNav (props) {
    const {el, ...otherProps} = props;
    const permissions = useSelector(state => state.user.value.level.permission);
    
 return (
    <Menu 
        id="profile" 
        anchorEl={el} 
        keepMounted 
        open={!!el}
        {...otherProps}
        PaperProps={{
            elevation: 0,
            sx: {
                overflow: 'visible',
                backdropFilter: 'blur(15px)',
                bgcolor: theme => theme.palette.background.paper + 'ee',
                border: theme => '1px solid '+ theme.palette.divider,
                m:0,
                p:0
            }
        }}
        // transformOrigin={{ horizontal: 'right', vertical: 'top' }}
        // anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
    >
       <Typography sx={{m: 1}} variant="h6" fontSize={18}>
                Applications
        </Typography>
        <Box
            sx={{
                height: 300
            }}
        >
            <Box
                sx={{
                    display: 'flex',
                    flex: 1,
                    width: 300,
                    m: 1,
                    flexDirection: 'row',
                }}
                component={Grid}
                container
            >
                {apps.sort().map((app, index) => (app.permissions ? isAuth(permissions, ...app.permissions) : true) && (
                    <Box
                        component={Grid}
                        item
                        key={index}
                        xs={3}
                    >
                        <Stack spacing={.1}>
                            <CardActionArea
                                sx={{
                                    display: 'flex',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                    p: .5,
                                }}
                                LinkComponent={Link}
                                to={app.path}
                            >
                                <ImgToIcon
                                    src={app.icon}
                                    size={50}
                                />
                            </CardActionArea>
                            <FormHelperText 
                                sx={{textAlign: 'center', textOverflow: 'ellipsis'}}
                            >{app.label}</FormHelperText>
                        </Stack>
                    </Box>
                ))}
            </Box>
        </Box>
    </Menu>
 )
}

const apps = [
    {
        label: 'Mediathèque',
        icon: medialibraryIconImage,
        path: '/apps/medialibrary',
        //permissions: ['bibliotheque', 'phototheque', 'filmotheque'],
    },
    {
        label: 'Archives',
        icon: archiveIconImage,
        path: '/apps/archives',
        permission: 'mediatheque',
        //permissions: ['archives'],
    },
    {
        label: 'Espace personnel',
        icon: workspaceIconImage,
        path: '/apps/workspaces',
        permissions: ['workspace'],
    },
    {
        label: 'Lisolo na budget', 
        icon: collaborationLounge,
        path: '/apps/livingroom',
        //path: ['workspace']
    },
    {
        label: 'Service de censure',
        icon: censorIconImage,
        path: '/apps/censorship',
        permissions: ['bibliotheque', 'phototheque', 'filmotheque', 'archives'],
    }
];