import {
      Box,
      Grid,
      Card,
      Chip,
      CardHeader,
      CardContent,
      CardActions,
      Divider,
      Button,
      Typography,
      useTheme,
      Paper,
      CardMedia,
} from '@mui/material';
import Header from './Header';
import BookPlan from '../../components/BookPlan';
import config from '../../config/app.json';
import capStr from '../../services/capStr';
import CardMediaPub from '../../components/CardMediaPub';
import { Link } from 'react-router-dom';
import { useSession } from '../../services/globalConfig';
import carouselItems from './carouselItems';
import cards from './cardItems';
import Carousel from 'react-material-ui-carousel';
import FormInputTheme from '../../config/FormInputTheme';
import useSize from '../../services/useSize';

export default function Home() {
    const theme = useTheme();
    const [,{getSession}] = useSession();
    const isAuth = getSession('user');
    const size = useSize();
    
    return (
        <Box>
            <Header/>
            <Carousel
              animation="slide"
              interval={8000}
              duration={1000}
              sx={{
                mb:1
              }}
            >
              {
                carouselItems.map((item, index) => (
                  <Paper 
                    sx={{position: 'relative',}} 
                    key={index} elevation={0}>
                    <CardMedia
                        component="img"
                        height={size.value({
                            xs: 270,
                            md: 260,
                            lg: 200,
                            xl: 200
                        })}
                        image={item.uri}
                        sx={{
                            filter: 'contrast(150%)',
                        }}
                    />
                    <Box 
                        sx={{
                            position: 'absolute', 
                            top: 0, 
                            left: 0, 
                            width: size.value({
                                xs: '85%',
                                md: '80%',
                                lg: '70%',
                                xl: "70%"
                            }), 
                            height: '100%',
                            color: 'white',
                            background: 'rgba(0,0,0,0.5)',
                        }}
                    >
                        <CardContent>
                            <Typography
                                fontSize={size.value({
                                    xs: 20,
                                    md: 20,
                                    lg: 25,
                                    xl: 30
                                })}
                            >{item.description}</Typography>
                                <FormInputTheme mode="dark">
                                    <Divider
                                        textAlign="right"
                                    >
                                        <Chip label={item.title}/>
                                    </Divider>
                                </FormInputTheme>
                        </CardContent>
                    </Box>
                  </Paper>
                ))
              }
            </Carousel>
            <Card sx={{mx: 1}} elevation={0}>
                <CardHeader
                    title="GEID"
                    subheader="Gestion Electronique de l'Information et des Documents"
                    sx={{
                        //border: '1px solid black',
                        mb: -3,
                    }}
                />
                <CardContent>
                    <Typography variant="body2" color="text.secondary">
                        La Gestion Electronique de l'Information et des Documents désigne l'ensemble
                        des techniques et pratiques nécessaires pour la prise en charge du flux
                        important des données, informations et documents au sein d'une organisation.
                        Cette solution informatique gère le cycle de vie des fichiers textes, sons,
                        images et vidéos. La GEID offre entre autres avantages : le gain de temps et
                        d’espace considérable avec son archivage électronique, une bibliothèque virtuelle
                        régulièrement à jour, une filmothèque et une photothèque bien fournie.
                    </Typography>
                </CardContent>
                {!isAuth &&
                <CardActions>
                    <Button
                        variant="outlined"
                        size="small"
                        LinkComponent={Link}
                        to="/signup"
                        color={theme.palette.mode === 'dark' ? "inherit" : "primary"}
                    >
                        s'inscrire a geid
                    </Button>
                </CardActions>}
            </Card>
            <Box>
                <Grid container spacing={1} sx={{p:1, pb: 0}}>
                    {documents.map((doc, index) => (
                        <Box 
                            key={index} 
                            component={Grid} 
                            item 
                            xs={size.value({
                                xs: 12,
                                md: 6,
                                lg: 3,
                                xl: 2
                            })}
                        >
                            <BookPlan
                                title={capStr(doc.title.toLowerCase())}
                                date=""
                                coverUrl={process.env.PUBLIC_URL + '/imgs/' + doc.image}
                                link={process.env.PUBLIC_URL + '/documents/' + doc.link}
                            />
                        </Box>
                    ))}
                </Grid>
            </Box>
            <Box>
                <Grid container spacing={1} sx={{p:1}}>
                    {cards.map((card, index) => (
                            <Box key={index} component={Grid} item 
                            xs={size.value({
                                xs: 12,
                                md: 6,
                                lg: 4,
                                xl: 4
                            })}>
                                <CardMediaPub
                                    title={card.title}
                                    content={card.content}
                                    href={card.href}
                                    modeLink="in"
                                    uri={card.src}
                                    access={card.accessMode === 'private' ? !!isAuth : true}
                                    navigateTo={card.redirectTo}
                                    message={card.message}
                                />
                            </Box>
                        ))}
                </Grid>
            </Box>
        </Box>
    );
}

const { documents } = config;
