import React, { forwardRef, useState, useRef, useEffect } from 'react';
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    Skeleton,
    useTheme,
    Box,
} from '@mui/material';
import FormInputTheme from '../config/FormInputTheme';
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';
import useAxios from 'axios-hooks';
import canvasToImg from '../services/canvasToImg';
import { getName } from '../services/getFileExtension';
import { LoadingButton } from '@mui/lab';
import { useDispatch, useSelector } from 'react-redux';
import serverPath from '../services/serverPath';
import { addUserKey } from '../redux/user';

export default function AvatarProfileEditor (props) {
    const token = useSelector(state => state.user.value.token);
    const [{data, loading}, refetch, cancel] = useAxios({
        method: 'post',
        url: PROFIL_URL,
        headers: { 'Authorization': `Bearer ${token}`, }
    }, {manual: true});
    const dispatch = useDispatch();
    // const [values, setValues] = useState({
    //   message: '',
    //   snackbar: false,
    //   error: false,
    // });
    const {file, ...otherProps} = props;
    const open= !!file;
    const theme = useTheme();
    const color = theme.palette.mode === 'dark' ? 'inherit' : 'primary';
    const cropData = useRef({
      targetEl: null,
      crop: {},
    });
    
    const handleSendProfile = () => {
      const {targetEl, crop} = cropData.current;
      canvasToImg(targetEl, crop).then(blob => {
        const blobToFile = new File([blob], 
          getName(file.name) + '.webp', {
          type: 'image/webp'
        });
        const data = new FormData();
        data.append('file', blobToFile);
        refetch({data});
      });
    };

    useEffect(() => {
      if(data) {
        dispatch(addUserKey(data));
        if(typeof props.onClose === 'function')
            props.onClose();
      }
          
    },[data, dispatch]);

  return (
    <React.Fragment>
      <FormInputTheme>
        <Dialog
          open={open}
          {...otherProps}
          //scroll={scroll}
          fullWidth
          BackdropProps={{
              sx: {
                  backdropFilter: 'blur(15px)'
              }
          }}
        >
          <DialogTitle id="scroll-dialog-title">Editeur profile</DialogTitle>
          <DialogContent dividers={true}>
              {file && <Editer ref={cropData} src={URL.createObjectURL(file)}/>}
          </DialogContent>
          <DialogActions>
            <Button 
              onClick={() => {
                cancel();
                cropData.current = {
                  targetEl: null,
                  crop: {},
                };
                props.onClose();
              }} 
              size="small" 
              color={color}
            >annuler</Button>
            <LoadingButton
              onClick={handleSendProfile} 
              size="small" 
              variant="outlined" 
              color={color}
              loading={loading}
            >ajouter</LoadingButton>
          </DialogActions>
        </Dialog>
      </FormInputTheme>
    </React.Fragment>
  );
}

const Editer = forwardRef((props, ref) => {
    const [crop, setCrop] = useState(((ref || {}).current || {}).crop);
    const [loading, setLoading] = useState(false);
    const targetEl = useRef(null);
    useEffect(() => {
        if(ref) ref.current = {
          crop,
          targetEl: targetEl.current,
        };
    });
    return (
      <Box
        sx={{
          flex: 1,
          display: 'flex',
          minHeight: 340,
        }}
      >
        <ReactCrop 
            crop={crop} 
            onChange={crop => setCrop(crop)}
            circularCrop={true}
            aspect={1}
        >
            <Box
                src={props.src}
                ref={targetEl}
                component="img"
                alt={props.src.toString()}
                onLoad={() => setLoading(true)}
                sx={{ display : loading ? '' : 'none'}}
            />
        </ReactCrop>
        {!loading &&
        <Skeleton 
          sx={{
            display: 'flex',
            width: '100%',
            height: 350,
          }}
          variant="rectangular"
          aniamation="wave"
        />}
      </Box>
    )
})

const PROFIL_URL = serverPath('/api/auth/profil');