import {
    Box,
    Tabs,
    Tab,
    AppBar ,
    Toolbar,
    Button,
    useTheme,
    Tooltip,
    IconButton,
    Stack,
    Chip,
    Avatar
} from '@mui/material';
import React,{ useState } from 'react';
import { Link } from 'react-router-dom';
import FromInputTheme from '../../config/FormInputTheme';
import SettingsRoundedIcon from '@mui/icons-material/SettingsRounded';
import { useConfig, useSession } from '../../services/globalConfig';
import AppsRoundedIcon from '@mui/icons-material/AppsRounded';
import ProfileNav from '../../components/ProfileNav';
import AppNav from '../../components/AppNav';
import links from './linkItems';
import ImgToIcon from '../../icons/ImgToIcon';
import useSize from '../../services/useSize';
import { useSelector } from 'react-redux';

export default function Header () {
    const [, actions] = useConfig();
    const [values, setValues] = useState({
        tab: 0,
        propfileNav: null,
        appNav: null,
    });
    const { imageUrl, lastName, isAuthenticated:isAuth} = useSelector(state => state.user.value);
    const theme = useTheme();
    const size = useSize();
    const isSmallScreen = ['xs', 'md'].find( key  => size.key === key);

    return (
        <React.Fragment>
            <AppBar
                sx={{
                    position: 'sticky',
                    top: 0,
                    backdropFilter: 'blur(15px)',
                    bgcolor: theme => theme.palette.background.paper + 'ee',
                }}
            >
                <Toolbar>
                    <FromInputTheme>
                        <Tabs
                            onChange={(event, value) => {
                                setValues({...values, tab: value})
                            }}
                            value={values.tab}
                            scrollButtons
                            //allowScrollButtonsMobile
                            variant="scrollable"
                            sx={{flexGrow: 1, ml: 1}}
                        >
                            {
                                links.map((link, index) => (
                                    RegExp(link.accessMode)
                                    .test( isAuth ? 'private' : 'public') &&
                                    <Tab
                                        label={isSmallScreen ? null : link.label}
                                        value={index}
                                        key={index}
                                        LinkComponent={Link}
                                        iconPosition="start"
                                        to={link.path}
                                        icon={
                                        <ImgToIcon
                                            src={link.icon}
                                            size={25}
                                            style={{
                                                filter: values.tab !== index ? 'grayscale(90%)' : 'none',
                                            }}
                                        />
                                    }
                                        disabled={link.disabled}
                                    />
                                ))
                            }
                        </Tabs>
                    </FromInputTheme>
                    <Stack spacing={1} direction="row">

                        <Box>
                            <Tooltip arrow title="Paramètre" enterDelay={700}>
                                <IconButton size="small"
                                    onClick={() => actions(null).handleChangeProps({settingsNav: true})}
                                >
                                    <SettingsRoundedIcon fontSize="small"/>
                                </IconButton>
                            </Tooltip>
                        </Box>
                            {isAuth ?
                            (
                            <React.Fragment>
                                <Box>
                                    <Tooltip arrow title="Applications" enterDelay={700}>
                                        <IconButton
                                            size="small"
                                            onClick={event => setValues({
                                                ...values,
                                                appNav: event.target
                                            })}
                                        >
                                            <AppsRoundedIcon fontSize="small"/>
                                        </IconButton>
                                    </Tooltip>
                                </Box>
                                <Box>
                                    <Tooltip arrow title="Profil" enterDelay={700}>
                                        <Chip
                                            avatar={<Avatar src={imageUrl}/>}
                                            label={lastName}
                                            variant="outlined"
                                            component="button"
                                            onClick={event => setValues({
                                                ...values,
                                                propfileNav: event.target
                                            })}
                                        />
                                    </Tooltip>
                                </Box>
                            </React.Fragment>
                            ) :
                            <Box>
                                <Button variant="outlined" size="small"
                                color={theme.palette.mode === 'dark' ? "inherit" : "primary"}
                                LinkComponent={Link}
                                to="/login"
                            >connexion</Button>
                            </Box>}
                    </Stack>
                </Toolbar>
            </AppBar>
            {isAuth &&
            <React.Fragment>
                <ProfileNav
                    onClose={()=> setValues({
                        ...values,
                        propfileNav: null,
                        })}
                    el={values.propfileNav}
                />
                <AppNav
                    onClose={()=> setValues({
                        ...values,
                        appNav: null,
                        })}
                    el={values.appNav}
                />
            </React.Fragment>}
        </React.Fragment>
    )
}
