import React, { useState } from 'react';
import {
    Card,
    CardContent,
    CardActionArea,
    CardMedia,
    Box,
    Typography,
    Skeleton,
    Dialog,
    DialogContent,
    DialogActions,
    Button,
    useTheme,
} from '@mui/material';
import capStr from '../services/capStr';
import { Link } from 'react-router-dom';

export default function CardMediaPub (props) {
    const {
        uri,
        content,
        title,
        href,
        modeLink,
        message,
        access,
        navigateTo,
    } = props;
    const [loaded, setLoad] = useState(false);
    const [open, setOpen] = useState(false);
    const theme = useTheme();
    const color = theme.palette.mode === 'dark' ? "inherit" : "primary";
    return (
        <React.Fragment>
            <Box
                {...(modeLink === 'out' ? {
                    LinkComponent: 'a',
                    href,
                }: modeLink === 'in' ? {
                    LinkComponent: Link,
                    to: href,
                } : {}
                )}
                component={CardActionArea}
                onClick={event => {
                    access || event.preventDefault();
                    if(!access)
                        setOpen(true)
                }}
            >
                <Card elevation={5}
                    sx={{
                            minHeight: 175,
                            display: 'flex',
                            flex: 1,
                            border: (theme) => `1px solid ${theme.palette.divider}`,
                        }}>
                    <Box sx={{ display: 'flex', flexGrow: 1 }}>
                        <CardContent>
                            <Typography
                                variant="h6"
                                component="div"
                            >{capStr(title)}</Typography>
                            <Typography
                                variant="body2"
                                component="div"
                                color="text.secondary"
                            >{content}</Typography>
                        </CardContent>
                    </Box>
                    <CardMedia
                        image={uri}
                        sx={{width: 165, display: loaded ? '' : 'none'}}
                        component="img"
                        onLoad={() => setLoad(true)}
                    />
                    {!loaded &&
                    <CardMedia>
                        <Skeleton
                            variant="rectangular"
                            animation="wave"
                            width={165}
                            //height={165}
                        />
                    </CardMedia>
                    }
                </Card>
            </Box>
            <Dialog
                open={open}
                onClose={() => setOpen(false)}
            >
                <DialogContent>
                    <Typography variant="body2">
                        {message}
                    </Typography>
                </DialogContent>
                <DialogActions>
                    <Button
                        color={color}
                        children="annuler"
                        onClick={() => setOpen(false)}
                        size="small"
                    />
                    <Button
                        children="se connecter"
                        color={color}
                        variant="outlined"
                        LinkComponent={Link}
                        to={navigateTo}
                        size="small"
                    />
                </DialogActions>
            </Dialog>
        </React.Fragment>
    )
}
