import React, { useRef, useState } from 'react';
import {
    Menu, 
    Box,
    CardContent, 
    CardHeader, 
    Avatar, 
    IconButton, 
    useTheme, 
    CardActions, 
    Button, 
    Divider, 
    Badge
} from '@mui/material';
import FromInputTheme from '../config/FormInputTheme';
import { useSession } from '../services/globalConfig';
import AddAPhotoRoundedIcon from '@mui/icons-material/AddAPhotoRounded';
import AvatarProfileEditor from './AvatarProfileEditor';
import { useDispatch, useSelector } from 'react-redux';
import { disconnecte } from '../redux/user';

export default function ProfileNav (props) {
    const [values, setValues] = useState({
        file: null,
    });
    const {el,...otherProps} = props;
    const [, {getSession, cleanSession}] = useSession();
    const { email, lastName, middleName, firstName } = getSession('user') || {}
    const isAuth = getSession('user');
    const theme = useTheme();
    const color = theme.palette.mode === 'dark' ? 'inherit' : 'primary';
    const { imageUrl } = useSelector(state => state.user.value);
    const dispatch = useDispatch();
    const fileInput = useRef();
    return ( isAuth &&
        <React.Fragment>
            <Menu 
                id="profile" 
                anchorEl={el} 
                keepMounted 
                open={!!el}
                {...otherProps}
                PaperProps={{
                    elevation: 0,
                    sx: {
                        overflow: 'visible',
                        backdropFilter: 'blur(15px)',
                        bgcolor: theme => theme.palette.background.paper + 'ee',
                        border: theme => '1px solid '+ theme.palette.divider,
                        m:0,
                        p:0
                    }
                }}
                transformOrigin={{ horizontal: 'right', vertical: 'top' }}
                anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
            >
                <Box
                    sx={{
                        //width: 400
                    }}
                >
                    <CardContent>
                        <FromInputTheme>
                            <CardHeader
                                sx={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                    flex: 1
                                }}
                            avatar={
                                <Badge
                                    overlap="circular"
                                    anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                                    badgeContent={
                                        <Box
                                            sx={{
                                                bgcolor: theme => theme.palette.background.paper + 'ee', 
                                                borderRadius: 25,
                                            }}
                                        >
                                            <IconButton 
                                                size="small"
                                                onClick={() => fileInput.current.click()}
                                            >
                                                <AddAPhotoRoundedIcon fontSize="small"/>
                                            </IconButton>
                                        </Box>
                                    }
                                >
                                    <Avatar 
                                        sx={{
                                            width: 70,
                                            height: 70,
                                            fontSize: 25,
                                            bgcolor: 'primary.main'
                                        }}
                                        src={imageUrl}
                                    aria-label="">
                                    {lastName?.charAt(0)}
                                    </Avatar>
                                </Badge>
                            }
                            title={`${lastName} ${middleName ? middleName + ' ': ''}${firstName}`}
                            titleTypographyProps={{
                                fontSize: 20,
                                variant: 'h6',
                                //paragraph: true,
                                align: 'center'
                            }}
                            subheader={email}
                            subheaderTypographyProps={{
                                align: 'center'
                            }}
                            />
                            <Divider/>
                                <CardActions
                                    sx={{justifyContent: 'center'}}
                                >
                                    <Button 
                                        color={color} 
                                        variant="outlined" 
                                        size="small"
                                        onClick={() => {
                                            cleanSession('user');
                                            dispatch(disconnecte());
                                        }}
                                    >
                                        deconnexion
                                    </Button>
                                </CardActions>
                            <Divider/>
                            <CardActions sx={{mb:0, pb:0}}>
                                    <Button   size="small" variant="text" color={color} sx={{flexGrow: 1, textTransform: 'none', fontSize: 12}}>
                                        Régles de confidentialité
                                    </Button>
                                    <Button size="small" variant="text" color={color} sx={{flexGrow: 1, textTransform: 'none', fontSize: 12}}>
                                        Conditions d'utilisation
                                    </Button>
                            </CardActions>
                        </FromInputTheme>
                    </CardContent>
                </Box>
            </Menu>
            <input 
                ref={fileInput} 
                type="file" 
                hidden 
                accept="image/*"
                onChange={event => {
                    const [file] = event.target.files;
                    if(file) {
                        setValues({
                            file: event.target
                            .files[0]
                        })}
                        props.onClose();
                    } 
                }
            />
            <AvatarProfileEditor
                file={values.file}
                onClose={() => setValues({
                    ...values, 
                    file: null
                })}
            />
        </React.Fragment>
    )
}